body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.headerWrapper{
  width: 80%;
  margin:0 auto;
  display: flex;
  justify-content: space-between;
  margin:40px auto; 
}
.title,.cartIcon {
  display: inline;
  margin:10px 50px;
}
.header{
  font-size: 25px;
  letter-spacing: 7px;
  text-decoration: none;
  color:rgb(18, 88, 97);
}
.checkoutWrapper{
  text-align: center;
  margin:20px;
}
.checkoutHeader{
  margin-top:20px;
}
.cartIcon p{
  padding:2px;
  margin: 2px;
  text-align: center;
  font-weight:bolder;
  border:2px solid green;
  border-radius: 50%;
}
.sCart{
  color: rgb(92, 92, 41);
  font-size: 25px;
}
.carList{
  display: flex;
  justify-content: space-evenly;
  flex-flow: row wrap;
}
.each-car{
  text-align:center;
  display: inline-block;
  margin:30px;
  list-style-type: none;
}
.each-car .car-title{
  margin: 0;
  display: block;
  font-size:20px;
  font-weight: bold; 
}
.each-car .car-image{
  -webkit-filter: contrast(160%);
          filter: contrast(160%); 
  height: 40%;
}
.each-car .car-info{
  text-decoration: none;
  color:rgb(17, 112, 88);
}
.each-car .car-buy,
.each-car .car-remove,
.back-button,.pay-button{
  padding: 5px;
  margin-left:15px;
  border-radius: 5px;
  outline: none;
  font-size:13px;
  background: rgb(0, 139, 93);
  color:white;
}
.each-car .car-price {
border:1px solid grey;
padding: 5px;
border-radius: 5px;
background: rgb(255, 238, 0);
font-weight: bold;
}
.each-car .car-remove{
  background: rgb(255, 0, 0);
  font-size:15px;
  font-weight: bold;
}
.back-button{
  position: absolute;
  left:0px;
  font-size:20px;
}
.back-icon{
  -webkit-transform: translate(-2px,3px);
          transform: translate(-2px,3px);
}
.total{
  position: absolute;
  right:0%;
  border:2px solid grey;
  list-style-type: none;
  padding: 0px 15px 10px 15px;
  background: whitesmoke;
}
.total-scale{
  border:2px solid black;
}
.que-list{
  width:70%
}
.que-list .car-image{
  height:50px;
}
.no-item{
  font-size:30px;
  color:rgb(255, 6, 6);
}
.pay-button{
 font-weight: bold;
 margin:10px;
 font-size:17px;
}

.pay-bills-form input{
  display: block;
 margin:10px 50px;
 padding: 10px;
}

